@import "../../styles/utils.scss";

.restaurant-card {
  --height: 367px;

  height: var(--height);
  width: 100%;

  cursor: pointer;

  @media (max-width: $breack-point-phone) {
    --height: 330px;
  }

  &__img {
    height: calc(var(--height) * 0.7);
    width: 100%;

    object-fit: cover;
  }

  &__title {
    margin: 12px 0 4px;
    font-size: 1.2em;
  }

  &__categories {
    font-size: 14px;
    color: #757575;

    margin-bottom: 4px;
  }

  &__eta {
    font-size: 14px;

    display: inline-block;
    padding: 2px 8px;

    background-color: #f5f5f5;
  }

  transition: transform 0.3s;

  &:hover {
    transform: scale(1.02);
    box-shadow: 0px 0px 47px -11px rgba(0,0,0,0.14);
  }
}
